.load-more-dark-theme {
  background: linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%);

  height: 38px;
  min-width: 106px;
  left: 107px;
  top: 4274px;
  border: none;
  border-radius: 74px;
  padding: 12px 24px 12px 24px;
  text-align: center;

  @include font(12px, 500, 14px, 0em);

  @media screen and (min-width: 768px) {
    height: 40px;
    min-width: 115px;
    left: 326px;
    top: 1595px;

    @include font(14px, 500, 16px, 0em);
  }

  @media screen and (min-width: 1280px) {
    height: 52px;
    min-width: 134px;
    left: 120px;
    top: 907px;

    @include font(16px, 500, 24px, 0em);
  }

  &:active,
  &:hover {
    border: 1px solid rgb(62, 62, 62);
  }
}

